<template>
  <b-container class="mt-4">
    <b-card no-body>
      <b-card-body class="px-md-5 pt-3 mt-2 mobile-scroll">
        <!-- Recruit logo starts here -->
        <div class="my-md-3 md-0 mobile-logo">
          <img src="/img/recruitly/logo.svg" alt="logo" class="img-fluid" />
        </div>
        <!-- Recruit logo ends here -->
        <b-row class="m-0 mt-4" no-gutters>
          <!-- Loading icon starts here -->
          <b-col md="12" v-if="loading">
            <div class="d-flex justify-content-center my-4">
              <b-spinner variant="primary" label="Spinning" />
            </div>
          </b-col>
          <template v-else>
            <b-col md="12">
              <!-- JOb title starts here -->
              <h4 class="font-weight-bold line-height-1-4 mb-4 text-body">{{ job_data[0].job_title }}</h4>
              <!-- JOb title ends here -->
              <!-- Job description starts here  -->
              <p class="mb-0 mt-1 align-middle text-gray-800" v-html="job_data[0].description" />
              <!-- Job description starts here -->
              <div class="mb-2 pb-1 d-flex flex-column align-items-md-center flex-md-row flex-wrap">
                <div class="text-muted mr-md-5 text-nowrap mb-3 mt-n2" v-if="job_data[0].no_of_position" style="line-height:1">
                  <span class="text-muted pr-2 mr-1">No of Positions :</span>
                  <span class="ml-n3 position-relative" style="top:2px">
                    <i class="fe fe-user" style="font-size: 11px !important;position: relative;top: -3px !important;left: 14px !important;" />
                    <i class="fe fe-search" style="font-size:19px !important" />
                    <span class="text-body position-relative" style="top: -2px !important;left: 1px !important;">{{ job_data[0].no_of_position }}</span>
                  </span>
                </div>
                <div class="text-muted mr-md-5 text-nowrap mb-3" v-if="job_data[0].no_of_applicants">
                  <span class="text-muted pr-2 mr-1">Total Applicants :</span>
                  <i class="fe fe-users mr-2" />
                  <span class="text-body">{{ job_data[0].no_of_applicants }}</span>
                </div>
                <!-- Location starts here -->
                <div class="text-muted mr-md-5 text-nowrap mb-3" v-if="job_data[0].location">
                  <span class="text-muted pr-2 mr-1">Job Locaton :</span>
                  <i class="fe fe-map-pin mr-2" />
                  <span class="text-body">{{ job_data[0].location }}</span>
                </div>
                <div class="text-muted mr-md-5 text-nowrap mb-3" v-if="job_data[0].type">
                  <span class="text-muted pr-2 mr-1">Job Type :</span>
                  <i class="fe fe-briefcase mr-2" />
                  <span class="text-body">{{ job_data[0].type }}</span>
                </div>
              </div>

              <div class="d-flex flex-column flex-md-row justify-content-md-between align-items-md-end align-items-baseline mt-n2 mt-md-0 mb-3">
                <small class="text-muted" v-if="job_data[0].created_date">
                  <span>Posted on :</span>
                  <i class="fe fe-calendar pl-2 pr-1" />
                  <span class="text-body">
                    {{ getLocalTime(job_data[0].created_date) }}
                  </span>
                </small>
                <div class="d-flex align-items-center">
                  <!-- Share Job option starts here -->
                  <b-button variant="link" size="sm" class="mr-3" @click="openShareJobModal(job_data[0])">
                    <i class="fe fe-share-2 mr-2" />
                    <span>Share</span>
                  </b-button>
                  <!-- Share Job option ends here -->

                  <!-- Apply Job option starts here -->
                  <b-button @click="showCandidateForm" size="sm" class="px-5 ml-auto text-nowrap mt-5 mt-md-4 mt-lg-0" variant="primary">
                    <i class="fe fe-check-circle mr-1" />
                    Apply
                  </b-button>
                  <!-- Apply Job option ends here -->
                </div>
              </div>
            </b-col>
          </template>
          <!-- Loading icon ends here -->
        </b-row>
      </b-card-body>
      <div class="footer-branding py-3 px-4 text-right border-top">
        <span>Powered by</span>
        <b-link target="_blank" href="http://500apps.com/" class="ml-1">
          <img src="/img/favicon.webp" class="ml-2" style="width: 17px;" />
          500apps
        </b-link>
      </div>
      <b-sidebar
        id="add_candidate"
        title="Apply"
        right
        shadow
        backdrop
        header-class="d-flex flex-row-reverse justify-content-between bg-white close-btn-right border-bottom"
        width="420px"
        body-class="bg-soft-light"
      >
        <!-- Add candidate form starts here -->
        <form ref="form" @submit.prevent="addCandidate">
          <b-form-group label="Name" label-class="pb-1" class="mb-3 pb-2">
            <b-form-input placeholder="Please enter your name" type="text" v-model="form.name" required />
          </b-form-group>
          <b-form-group label="Email" label-class="pb-1" class="mb-3 pb-2">
            <b-form-input placeholder="Please enter your email" type="email" v-model="form.email" required />
          </b-form-group>
          <b-form-group label="Resume" label-class="pb-1" class="mb-3 pb-2">
            <b-form-file
              @submit.stop.prevent
              @input="uploadResume"
              ref="file_input"
              accept=".doc,.docx,.pdf,.txt,.png, .ppt,.rtf,.tex,.jpg,.jpeg,.xls,.xlsx,.csv"
              class="mb-2"
              placeholder="Please upload your resume"
              drop-placeholder="Drop file here..."
              v-model="form.resume_upload"
            ></b-form-file>
            <span class="text-muted"> Supported files formats are .doc, .docx, .pdf, .txt, .png, .ppt, .rtf, .tex, .jpg, .jpeg, .xls, .xlsx and .csv </span>
          </b-form-group>
          <b-form-group label="Primary Contact" label-class="pb-1" class="mb-3 pb-2 mobile-number">
            <VueTelInput type="tel" name="telephone" v-model="form.primary_contact" mode="international" autocomplete="tel" :key="render_phone" />
          </b-form-group>
          <b-form-group class="recaptcha-mr-0" v-if="recaptcha_site_key && recaptcha_secret_key">
            <Recaptcha :site_key="repatcha_site_key" :secret_key="repatcha_secret_key" />
            <span v-if="validate_recaptcha" class="text-danger">Please select or configure recaptcha</span>
          </b-form-group>
          <div class="d-flex align-items-center justify-content-end border-top pt-4 mt-3">
            <b-badge variant="soft-danger" class="font-weight-500 py-2 px-3 rounded-sm" v-if="error_message">Duplicate entry</b-badge>
            <b-button variant="primary" class="px-5 ml-4" type="submit">{{ submit_button }}</b-button>
          </div>
          <b-badge variant="soft-success" class="font-weight-500 py-2 px-3 mt-2 rounded-sm" v-if="submit_button == 'Submitted'"
            >Thank you {{ form.name }} for applying to the {{ job_data[0].job_title }}.</b-badge
          >
        </form>
        <!-- Add candidate form ends here -->
      </b-sidebar>
      <!-- Share job sidebar starts here -->
      <b-sidebar
        id="share-job"
        title="Share Job"
        right
        shadow
        backdrop
        header-class="d-flex flex-row-reverse justify-content-between bg-white close-btn-right border-bottom"
        width="420px"
        body-class="bg-soft-light"
      >
        <form ref="form" @submit.prevent="sendEmail('view-job')">
          <b-form-group label="To Email" label-class="pb-1" class="mb-3 pb-2">
            <b-form-input placeholder="Please enter your email" type="email" v-model="share_to_email" required />
          </b-form-group>
          <div class="d-flex align-items-center justify-content-end border-top pt-4 mt-3">
            <b-badge variant="soft-success" class="font-weight-500 py-2 px-3 rounded-sm" v-if="send_email_status">Sent Successfully</b-badge>
            <b-button variant="primary" class="px-5 ml-4" type="submit">{{ send_email_button_status }}</b-button>
          </div>
        </form>
      </b-sidebar>
      <!-- Share job sidebar starts here -->
    </b-card>
  </b-container>
</template>
<script>
  import JobDetails from "@/js/recruit/job-details";
  import Candidate from "@/js/recruit/candidate";
  import FileManager from "@/js/core/file-manager";
  import DefaultData from "@/js/recruit/default-data";
  import SendEmailData from "@/js/recruit/send-email-data";
  import { VueTelInput } from "vue-tel-input";
  import Recaptcha from "../form/components/input/Recaptcha";
  import RecaptchaDetails from "@/js/recruit/get-recaptcha-details";

  export default {
    components: { VueTelInput, Recaptcha },
    data() {
      return {
        domain_id: "",
        group_id: "",
        job_id: "",
        job_data: [],
        form: {
          name: "",
          email: "",
          resume_upload: "",
          primary_contact: "",
        },
        validate_recaptcha: false,
        enable_add_candidate: false,
        recaptcha_response_status: false,
        submit_button: "Submit",
        error_message: false,
        render: 0,
        loading: true,
        share_to_email: "",
        row_data: {},
        send_email_status: false, // Show email success message
        send_email_button_status: "send", // Change button content send,sending and sent.
        render_phone: 0,
        recaptcha_site_key: "",
        recaptcha_secret_key: "",
      };
    },
    mounted() {
      this.domain_id = window.domain;
      this.group_id = window.user;
      this.job_id = this.$route.params.job_id;
      this.getJobDetails();
    },
    methods: {
      // Get Job details by job_id
      async getJobDetails() {
        // Get job details by group id,domain id and job id
        this.job_data = await JobDetails.getJobDetailsById(this.domain_id, this.job_id);
        if (this.job_data) {
          this.group_id = window.user ? window.user : this.job_data[0].group_id;
          this.loading = false;
        }
      },
      // Remove html tags from description text
      getDescription(description) {
        return description.replace(/(<([^>]+)>)/g, "");
      },
      // Show add candidate form
      async showCandidateForm() {
        ++this.render_phone;
        this.submit_button = "Submit";
        this.error_message = false;
        // Open candidate side bar
        this.$root.$emit("bv::toggle::collapse", "add_candidate");
        this.enable_add_candidate = true;
        // Get recaptcha data
        this.$root.$on("recaptcha-callback-response", this.getRecaptcha);
        this.form = {};
        this.form.job_id = this.job_id;

        // Get plugins recaptcha info (Ref: get-recaptcha-details.js )
        let response = await RecaptchaDetails.getRecaptchaKeys(".recruithire.com", "recruit", this.domain_id, "recruithire", this.group_id);
        if (response) {
          this.recaptcha_site_key = response.recaptcha_site_key;
          this.recaptcha_secret_key = response.recaptcha_secret_key;
        }
      },
      // Get recaptcha response
      getRecaptcha(response) {
        // Get response after selecting recaptcha in add candidate form
        this.recaptcha_response_status = response ? true : false;
        // Show validation message if recaptcha status is false
        this.validate_recaptcha = this.recaptcha_response_status ? false : true;
      },
      // Get uploaded resume URL
      async uploadResume(file) {
        // Get s3 url after uploading resume upload in add candidate form
        let upload_data = await FileManager.upload(`recruit/${this.group_id}/${this.job_id}`, "post-object", file, "", file.type, "public-read");
        let data = upload_data.presign_data;
        // Assign s3 url to form
        this.form.resume_upload = `${data.url}${data.fields.key}`;
      },
      // Add a candidate
      async addCandidate() {
        // check recaptcha response is true or not
        if (this.recaptcha_response_status) {
          this.submit_button = "Submitting";
          this.form.source = "website";
          try {
            // Split name with empty-space and assign to respective fields
            let name = this.form.name.split(" ");
            this.form.first_name = name[0] ? name[0] : "";
            this.form.last_name = name[1] ? name[1] : "";
            let candidate_response = await Candidate.saveCandidate(this.domain_id, this.group_id, this.form);
            // Check candidate is added or not and if yes, close add candidate sidebar
            if (candidate_response.status) {
              this.submit_button = "Submitted";
              setTimeout(() => {
                ++this.render;
                this.$root.$emit("bv::toggle::collapse", "add_candidate");
                this.submit_button = "Submit";
              }, 3000);
            }
          } catch (error) {
            ++this.render;
            this.submit_button = "Submit";
            this.error_message = true;
          }
        } else this.validate_recaptcha = true;
      },
      // Get local time
      getLocalTime(value) {
        return DefaultData.dateFormat(value);
      },
      // Open share job model
      openShareJobModal(data) {
        this.share_to_email = "";
        this.row_data = data;
        this.$root.$emit("bv::toggle::collapse", "share-job");
      },
      ...SendEmailData, // Ref: send-email-data.js
    },
  };
</script>
<style>
  @import url("/css/sidebar/sidebar.css?v=infinity_release_tag");
</style>
